.styled-table {
    //border-collapse: collapse;
    margin: 25px 0;
    font-size: 0.9em;
    font-family: sans-serif;
    min-width: 400px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  }
  
  .styled-table thead tr {
    background-color:  #5f5f5f; // #009879;
    color: #ffffff;
    text-align: left;
  }
  
  .styled-table th,
  .styled-table td {      
      padding: 4px 5px;      // padding: 12px 15px;   
  }
  
  .styled-table tbody tr {
    border-bottom: thin solid #dddddd;
  }
  
  .styled-table tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
  }
  
  .styled-table tbody tr:last-of-type {
    border-bottom: 2px solid #5f5f5f; // #009879;
  }
  
  .styled-table tbody tr.active-row {
    font-weight: bold;
    color: #5f5f5f;// #009879;
  }
  
  .styled-table tbody tr.appelee-row-highlight {
    background-color: #ffd6b0bb;
  }

  .styled-table tbody tr.appelante-row-highlight {
    background-color: #c5e4f0d5;
  }  

  .styled-table tbody td.appelee-highlight {
    background-color: #ffd6b0bb;
  }

  .styled-table tbody td.appelante-highlight {
    background-color: #c5e4f0d5;
  }  