.footnotes ol li {
    list-style-type: decimal;
}

.footnotes ol {
    font-size: .85em;
    color: #666666;
}

html {
    overflow-y: scroll;
}

ul.listing {
    list-style-type: none;
    margin-left: 0px;
}

ul.listing li.listing-seperator {
    padding-top: 15px;
    font-weight: bold;
    font-family: 'Titillium Web', sans-serif;
    font-size: 1.17em;
}

ul.listing li.listing-item time {

    color: #333;
    font-weight: 500;
    text-transform: uppercase;
    padding-right: 10px;
}

ul.listing li.listing-item a {
    color: $clemson-purple;
    font-weight: 400;
    font-family: 'Titillium Web', sans-serif;
}


/*------ archive type stuff ------ */

ul#archive {
    list-style-type: none;
    margin: 0 0 1em 0;
    padding: 0;
}

ul#archive li {
    //    border-top: #999 1px dotted;
    border-bottom: #999 1px dotted;
    padding: .1em 0 .1em .5em;
    background: #fff;
}

ul#archive h2 {
    padding-top: .5em
}

ul#archive li.alt {
    background: #f6f6f6;
}

.archiveposturl {
    display: block;

    color: #333;
}

.archiveposturl:hover {}

ul#archive li span a {
    color: $clemson-purple;
    font-weight: bold;
    font-family: 'Titillium Web', sans-serif;
}


ul#archive li span.postlower {
    font-size: 85%
}

ul#archive li span.postlower a {
    color: #07c;
    font-weight: normal;
    font-family: "Open Sans", Helvetica, Arial, sans-serif;

}

article ul {
    list-style-type: none;
}

article ul>li:before {
    content: "–";
    /* en dash */
    position: absolute;
    margin-left: -1.1em;
}

article ul#archive>li:before {
    list-style-type: none;
    content: none;
}


