body {
    font-size:15px;
}

.bs-callout {
    padding: 20px;
    margin: 20px 0;
    border: 1px solid #eee;
    border-left-width: 5px;
    border-radius: 3px;
}
.bs-callout h4 {
    margin-top: 0;
    margin-bottom: 5px;
}
.bs-callout p:last-child {
    margin-bottom: 0;
}
.bs-callout code {
    border-radius: 3px;
}
.bs-callout+.bs-callout {
    margin-top: -5px;
}
.bs-callout-default {
    border-left-color: #777;
}
.bs-callout-default h4 {
    color: #777;
}
.bs-callout-primary {
    border-left-color: #428bca;
}
.bs-callout-primary h4 {
    color: #428bca;
}
.bs-callout-success {
    border-left-color: #5cb85c;
}
.bs-callout-success h4 {
    color: #5cb85c;
}
.bs-callout-danger {
    border-left-color: #d9534f;
}
.bs-callout-danger h4 {
    color: #d9534f;
}
.bs-callout-warning {
    border-left-color: #f0ad4e;
}
.bs-callout-warning h4 {
    color: #f0ad4e;
}
.bs-callout-info {
    border-left-color: #5bc0de;
}
.bs-callout-info h4 {
    color: #5bc0de;
}

.checkoff{
    margin-bottom:1em;
    border:1px dotted currentColor;
    border-radius:.25em;
    padding:.25em .5em;
    background-color: hsl(21, 100%, 95%);
    color:hsl(33, 100%, 4%)
}

.checkoff code{
    background-color: hsl(21, 100%, 95%);
    color: currentColor;
}

.checkoff legend{
    font-family: 'Titillium Web', sans-serif;
    margin-left: 1em;
    margin-bottom:1em;
    border:1px dotted hsl(33, 100%, 4%);
    border-radius:.25em;
    padding:.25em .5em;
    padding: 0.2em 0.8em;
    background-color: hsl(21, 100%, 95%);    
    color: $title-color;
    font-weight: bold;
}


.tips{
    margin-bottom:1em;
    border:1px dotted currentColor;
    border-radius:.25em;
    padding:.25em .5em;
    background-color: hsl(67, 100%, 95%);
    color:hsl(33, 100%, 4%)
}

.tips code{
    background-color: hsl(67, 100%, 95%);
    color: currentColor;
}

.tips legend{
    font-family: 'Titillium Web', sans-serif;
    margin-left: 1em;
    margin-bottom:1em;
    border:1px dotted hsl(33, 100%, 4%);
    border-radius:.25em;
    padding:.25em .5em;
    padding: 0.2em 0.8em;
    background-color:  hsl(67, 100%, 95%);
    color: $title-color;
    font-weight: bold;
}






